<template>
  <div class="login">
    <div class="login-panel">
      <h3>Login</h3>
      <b-form-group label="Name:" label-for="new-name">
          <b-form-input id="new-name" type="text" v-model="name" required placeholder="Insira o seu email"></b-form-input>
      </b-form-group>
      <b-form-group label="Título:" label-for="new-title">
          <b-form-input id="new-title" type="password" v-model="password" required placeholder="Insira a sua senha"></b-form-input>
      </b-form-group>
      <div class="buttons">
          <b-button @click="navigate('/')" >Cancelar</b-button>
          <b-button variant="success" @click="login()">Entrar</b-button>   
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import { url, showError, userKey } from '@/config/global';

export default {
  name: 'Login',
  data: function(){
    return {
      name: null,
      password: null
    }
  },
  methods: {
    navigate(local){
      this.$router.push({path: local})
    },
    login(){
      axios.post(`${url}/login`, {'email': this.name, 'password': this.password})
      .then(res => {
          this.$store.commit('setUser', res.data)
          localStorage.setItem(userKey, JSON.stringify(res.data))
          this.$router.push({path: '/admin/publication'})
          this.$toasted.global.defaultSuccess({msg:"Successfully Logged In"})
      })
      .catch(showError)
    },
  }
}
</script>

<style>
.login {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.login-panel {
  background-color: #90a4ae;
  border-radius: 10px;
  color: white;
  padding: 30px;
  width: 30%;
}
.login .buttons{
  display: flex;
  align-items: center;
  justify-content: space-between;
}
</style>
