import Vue from 'vue'
import VueRouter from 'vue-router'

import Client from '@/components/Client'
import Admin from '@/components/Admin'
import Login from '@/components/Login'

import Home from '@/components/pagesclient/contents/Home'
import Categories from '@/components/pagesclient/contents/Categories'
import Publication from '@/components/pagesclient/contents/Publication'

import AdminPublication from '@/components/pagesadmin/AdminPublication'
import AdminCategory from '@/components/pagesadmin/AdminCategory'

Vue.use(VueRouter)

const router = new VueRouter({
    //mode: 'history',
    history: true,
    routes: [
        {
            name: 'Client',
            path: '/',
            component: Client,
            children: [
                {
                  path: '/',
                  name: 'Home',
                  component: Home,
                },
                {
                path: '/categories',
                name: 'Categories',
                component: Categories,
                },
                {
                path: '/publication/:id',
                name: 'Publication',
                component: Publication,
                props: true
                }
              ]
        },
        {
            name: 'Admin',
            path: '/admin',
            component: Admin,
            children: [
                {
                  path: '/admin/publication',
                  name: 'AdminPublication',
                  component: AdminPublication,
                },
                {
                    path: '/admin/category',
                    name: 'AdminCategory',
                    component: AdminCategory,
                  }
              ]
        },
        {
            name: 'Login',
            path: '/login',
            component: Login
        },
    ]
})

export default router