<template>
  <router-view></router-view>
</template>

<script>
import axios from 'axios';
import { url, userKey } from '@/config/global';
export default {
	name: 'App',
	methods: {
		async validateToken() {
			const json = localStorage.getItem(userKey)
			const userData = JSON.parse(json)
			this.$store.commit('setUser', null)
			if(!userData)
			{				
				this.$store.commit('setUser', null)
				return
			}
			
			const res = await axios.post(`${url}/validate`, userData)

			if(res.data)
			{
				this.$store.commit('setUser', userData)
			}
			else
			{
				localStorage.removeItem(userKey)
				this.$store.commit('setUser', null)
			}
		}
    },
	created(){
		this.validateToken()
	}
}
</script>

<style>
* {
    font-family: 'Indie Flower', cursive;
}
html, body {
  margin: 0;
  height: 100%;
}
</style>
