<template>
  <Loading v-if="loading" />
  <div class="home" v-else>
  <div class="home-title">Bem vindo! Confira as principais publicações!</div>
  <b-container class="home-content">
    <b-row>
        <b-col v-for="item in data" :key="item._id" @click="navigate(item._id)" >
            <div class="publicationBox">
              <img v-bind:src="item.image"/>
              <div class="publicationBox-info">
                <div class="boxcategory">{{item.category_name}}</div>
                <div class="boxtitle">{{item.title}}</div>
                <div class="boxsubtitle">{{item.subtitle}}</div>
              </div>
            </div>
        </b-col>
    </b-row>
  </b-container>
  </div>
</template>

<script>
import axios from 'axios';
import {url, showError} from '@/config/global';
import Loading from "@/components/Loading";
export default {
  name: 'Home',
  components: {
    Loading
  },
  data: function(){
    return {
      data: [],
      loading: 0
    }
  },
  methods: {
      navigate(id){
        this.$router.push(`/publication/${id}`);
      },
      loadPublications() {
          this.loading = 1
          axios.get(`${url}/publication-favorites`)
          .then(res => {
            this.data = res.data
            this.loading = 0
          }).catch(showError)
      }
  },
  beforeMount () {
      this.loadPublications()
  }
}
</script>

