<template>
  <div class="publication-box">
    <Title msg="Administração" logoutButtom="1"></Title>
    <AdminMenu />
    <router-view></router-view>
  </div>
</template>

<script>
import axios from 'axios';
import {url, userKey} from '@/config/global';
import Title from '@/components/pagesclient/Title';
import AdminMenu from '@/components/pagesadmin/AdminMenu';

export default {
  name: 'Admin',
  components: {
    Title, AdminMenu
  },
  data: function () {
        return {
            fields: ["name", "title", "subtitle","ações"],
            items: [],
            edit: false,
            new_publication: {
              name: null,
              title: null,
              subtitle: null,
              content: null,
              removed: 0
            },
        }
  },
  methods: {
      async validateToken() {
        try {
          const json = localStorage.getItem(userKey)
          const userData = JSON.parse(json)
          this.$store.commit('setUser', null)       
          const res = await axios.post(`${url}/validate`, userData)
          
          if(res.data)
          {
            this.$store.commit('setUser', userData)
          }
          else
          {
            localStorage.removeItem(userKey)
            this.$store.commit('setUser', null)
            this.$router.push({path: '/login'})
          }
        }catch(e){
          this.$router.push({path: '/login'})
        }
      }
  },
  beforeMount(){
      this.validateToken()
  }
}
</script>

