<template>
    <div class="admin-publication">
      <div class="admin-publication-create">
        <h3 v-if="!edit">Criar uma publicação</h3>
        <h3 v-if="edit">Editar uma publicação</h3>
        <b-form-group label="Título:" label-for="new-title">
            <b-form-input id="new-title" type="text" v-model="new_publication.title" required placeholder="Insira um título para a publicação"></b-form-input>
        </b-form-group>
        <b-form-group label="Subtítulo:" label-for="new-subtitle">
            <b-form-input id="new-subtitle" type="text" v-model="new_publication.subtitle" required placeholder="Insira um subtítulo para a publicação"></b-form-input>
        </b-form-group>
        <b-form-group class="newpublication-content" label="Conteúdo:" label-for="new-content">
            <span v-for="(cont,index) in new_publication.content" :key="index">
              <span v-if="cont.type == 'text'" class="image-and-uploader">
                <b-button class="m-1" size="sm" variant="danger" @click="removeContent(index);"><i class="fa fa-trash" ></i></b-button>
                <b-form-textarea  id="new-content" type="text" v-model="new_publication.content[index].data" required placeholder="Insira o conteúdo"></b-form-textarea>
              </span>
              <span v-if="cont.type == 'video'" class="image-and-uploader">
                <b-button class="m-1" size="sm" variant="danger" @click="removeContent(index);"><i class="fa fa-trash" ></i></b-button>
                <b-form-input  id="new-content-video" type="text" v-model="new_publication.content[index].data" required placeholder="Insira o link do video"></b-form-input>
              </span>  
              <span v-if="cont.type == 'image'" class="image-and-uploader">
              <b-button class="m-1" size="sm" variant="danger" @click="removeContent(index);"><i class="fa fa-trash" ></i></b-button>
              <img v-bind:src="new_publication.content[index].data" style="height: 50px;" /> 
              <ImageUploader 
                className="image-uploader"
                :debug="1"
                :maxWidth="384"
                :maxHeight="384"
                :quality="0.7"
                :autoRotate=true
                outputFormat="verbose"
                :preview=false
                :capture="false"
                accept="image/*"
                doNotResize="['gif', 'svg']"
                @input="setContentImage($event,index)"
                @onUpload="setContentImage($event,index)"
                @onComplete="setContentImage($event,index)"
              ></ImageUploader>
              </span>
            </span> 
            <b-button class="m-1" @click="addContent('text');">
                  add <i class="fa fa-align-left"></i>
            </b-button>
            <b-button class="m-1" @click="addContent('image');">
                  add <i class="fa fa-image"></i>
            </b-button>
            <b-button class="m-1" @click="addContent('video');">
                  add <i class="fa fa-youtube"></i>
            </b-button>
        </b-form-group>

          <b-form-group label="Categoria:" label-for="new-category">
              <b-dropdown  id="new-category" dropup :text="selected_category">
                  <b-dropdown-item v-for="cat in categories" 
                      :key="cat._id" 
                      :value="cat" @click="
                      new_publication.category_id = cat._id;
                      new_publication.category_name = cat.name;
                      selected_category = cat.name;" >{{cat.name}}</b-dropdown-item>
              </b-dropdown>
          </b-form-group>
          <b-form-group label="Capa da publicação:" label-for="new-image">
            <span class="image-and-uploader">
            <img v-bind:src="new_publication.image" style="height: 50px;" />   
            <ImageUploader
            :debug="1"
            :maxWidth="384"
            :maxHeight="384"
            :quality="0.7"
            :autoRotate=true
            outputFormat="verbose"
            :preview=false
            :capture="false"
            accept="image/*"
            doNotResize="['gif', 'svg']"
            @input="setCoverImage"
            @onUpload="setCoverImage"
            @onComplete="setCoverImage"
          ></ImageUploader>
          </span>
        </b-form-group>
        <div class="buttons">
            <b-button @click="reset">Cancelar</b-button>
            <b-button v-if="!edit" variant="success" @click="create">Criar</b-button>
            <b-button v-if="edit" variant="success" @click="update">Editar</b-button>   
        </div>
      </div>
      <div class="admin-publication-list">
        <Loading v-if="loading" />
        <b-table v-else striped hover :items="items" :fields="fields">
          <template slot="image" slot-scope="data">
                <img v-bind:src="data.item.image" style="width: 70px;" />                   
          </template>
          <template slot="editar" slot-scope="data">
              <b-button class="mr-1" variant="warning"  @click='edit = true; new_publication = data.item'>
                  <i class="fa fa-pencil"></i>
              </b-button>           
          </template>
           <template slot="ativar" slot-scope="data">  
              <b-button class="mr-1" v-if="!data.item.removed" variant="success"  @click='remove(data.item);'>
                  <i class="fa fa-check-circle"></i>
              </b-button>
              <b-button class="mr-1" v-if="data.item.removed" variant="danger"  @click='remove(data.item);'>
                  <i class="fa fa-times-circle"></i>
              </b-button>                      
          </template>
           <template slot="favoritar" slot-scope="data">
              <b-button class="mr-1" v-if="!data.item.favorited" @click='favorite(data.item);'>
                  <i class="fa fa-star"></i>
              </b-button>
              <b-button class="mr-1" v-if="data.item.favorited" variant="warning" @click='favorite(data.item);'>
                  <i class="fa fa-star"></i>
              </b-button>                        
          </template>
        </b-table>
      </div>
    </div>
</template>

<script>
import axios from 'axios';
import {url, showError} from '@/config/global';
import Loading from "@/components/Loading";
import ImageUploader from 'vue-image-upload-resize'

export default {
  name: 'AdminPublication',
  components: {
    Loading, ImageUploader
  },
  data: function () {
        return {
            loading: 0,
            selected_category: null,
            fields: ["image","title", "subtitle","category_name","editar","ativar","favoritar"],
            items: [],
            categories: [],
            edit: false,
            new_publication: {
              title: null,
              subtitle: null,
              content: [],
              image: null,
              category_id: null,
              category_name: null,
              removed: 0,
              favorited: 0
            },
        }
  },
  methods: {
      addContent(type){
        this.new_publication.content.push({type: type, data: null})
      },
      removeContent(index){
        this.new_publication.content.splice(index,1)
      },
      setContentImage: function (file, index) {
        this.hasImage = true
        if (file != undefined) {
          this.new_publication.content[index].data = file.dataUrl
        }
      },
      setCoverImage: function (file) {
        this.hasImage = true
        if (file != undefined) {
            this.new_publication.image = file.dataUrl
        }
      },
      loadPublications() {
          this.loading = 1
          axios.get(`${url}/publication`)
              .then(res => {
                this.items = res.data
                this.loading = 0
              })
              .catch(showError)
      },
      loadCategories() {
          axios.get(`${url}/category`)
              .then(res => {
                this.categories = res.data
              })
              .catch(showError)
      },
      create(){
          axios.post(`${url}/publication`,this.new_publication)
              .then(() => {
                this.loadPublications()
                this.$toasted.global.defaultSuccess({msg:"Publicação cadastrado!"})
                this.reset()
              })
              .catch(showError)
              
      },
      update(){
          axios.put(`${url}/publication`,this.new_publication)
          .then(() => {
            this.loadPublications()
            this.$toasted.global.defaultSuccess({msg:"Publicação atualizada!"})
            this.reset()
          })
          .catch(showError)
              
      },
      remove(item){
        axios.put(`${url}/publication`,{...item, 'removed': !item.removed})
          .then(() => {
            this.loadPublications()
            this.$toasted.global.defaultSuccess({msg:"Publicação atualizada!"})
            this.reset()
          })
          .catch(showError)
      },
      favorite(item){
        axios.put(`${url}/publication`,{...item, 'favorited': !item.favorited})
          .then(() => {
            this.loadPublications()
            this.$toasted.global.defaultSuccess({msg:"Publicação atualizada!"})
            this.reset()
          })
          .catch(showError)
      },
      reset(){
        this.edit = false;
        this.new_publication = {
          title: null,
          subtitle: null,
          content: [],
          image: null,
          category_id: null,
          category_name: null,
          removed: 0,
          favorited: 0
        }
      }
  },
  mounted() {
      this.loadPublications()
      this.loadCategories()
  },
  created() {
      this.loadPublications()
      this.loadCategories()
  }
}
</script>

<style>
.admin-publication {
  padding: 30px;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  flex-direction: row;
}
.admin-publication-list{
  width: 70%;
}
.admin-publication-create {
  width: 25%;
}
.admin-publication-create .buttons{
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.admin-publication-item{
  display: flex;
  justify-content: space-between;
}

.admin-publication-item > i:hover{
  cursor: pointer;
}

.admin-publication .create{
  padding: 50px;
}
.admin-publication .create .buttons{
  display: flex;
  justify-content: space-between;
}
.image-and-uploader{
  overflow: hidden;
  padding: 5px;
  display: flex;
  margin: 10px;
  justify-content: flex-start;
}

.image-and-uploader img {
  margin-right: 5px;
}

.newpublication-content {
  border: 1px solid black;
  padding: 5px;
  border-radius: 5px;
}
</style>
