<template>
  <span>
    <div v-if="logoutButtom == '0' " class="title">
      <span>{{ msg }}</span>
    </div>
    <div v-if="logoutButtom == '1' " class="title title-logout">
      {{ msg }}
      <span>
        <b-button size="sm" variant="outline-dark" style="margin-right: 10px;" @click="navigate()">voltar para o blog</b-button>
        <b-button size="sm" variant="outline-dark" @click="logout">logout</b-button>
      </span>
    </div>
  </span>
</template>

<script>
import {userKey} from '@/config/global';
export default {
  name: 'Title',
  props: {
    msg: String,
    logoutButtom: String
  },
  methods: {
    navigate(){
      this.$router.push('/')
    },
    logout(){
      localStorage.removeItem(userKey)
      this.$store.commit('setUser',null)
      this.$router.push('/')
      this.$toasted.global.defaultSuccess({msg:"Successfully Logged Out"})
    },
  }
}
</script>

<style>
.title {
  grid-area: title;
  background-color: white;
  color: black;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  font-size: 1.7rem;
  font-weight: bold;
}

.title span {
  padding-left: 30px;
}

.title-logout {
  justify-content: space-between;
  padding: 0 10px;

}
</style>
