<template>
  <Loading v-if="loading" />
  <div class="publication" v-else>
      <h1>{{data.title}}</h1>
      <h3>{{data.subtitle}}</h3>
      <span v-for="(cont,index) in data.content" :key="index">
        <div v-if="cont.type=='text'">{{cont.data}}</div>
        <img v-if="cont.type=='image'" :src="cont.data">
        <iframe v-if="cont.type=='video'" frameborder="0" width="560" height="315"
        allowfullscreen :src="'https://www.youtube.com/embed/'+cont.data" ></iframe>
      </span>
  </div>
</template>

<script>
import axios from 'axios';
import {url, showError} from '@/config/global';
import Loading from "@/components/Loading";
export default {
name: 'Title',
  components: {
      Loading
  },
  props: {
    id: String
  },
  data: function(){
    return {
      loading: false,
      data: null
    }
  },
  methods: {
    loadPublication(){
      this.loading = 1
          axios.get(`${url}/publication/${this.id}`)
          .then(res => {
            this.data = res.data[0]
            this.loading = 0
            console.log(this.data)
          }).catch(showError)
    }
  },
  beforeMount () {
      this.loadPublication()
  }
}
</script>

<style>
.publication {
  padding: 0 50px;
  padding-top: 30px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.publication span{
  margin: 15px 0;
}
</style>