<template>
    <div class="category">
      <div class="category-create">
        <h3 v-if="!edit">Criar uma nova Categoria</h3>
        <h3 v-if="edit">Editar uma Categoria</h3>
        <b-form-group label="Nome:" label-for="new-name">
            <b-form-input id="new-name" type="text" v-model="new_category.name" required placeholder="Insira um nome para a Categoria"></b-form-input>
        </b-form-group>
        <b-form-group label="Título:" label-for="new-title">
            <b-form-input id="new-title" type="text" v-model="new_category.title" required placeholder="Insira um título para a Categoria"></b-form-input>
        </b-form-group>
        <b-form-group label="Capa da categoria:" label-for="new-image">
          <img v-bind:src="new_category.image" style="width: 70px;" />
          <ImageUploader
            :debug="1"
            :maxWidth="384"
            :maxHeight="384"
            :quality="0.7"
            :autoRotate=true
            outputFormat="verbose"
            :preview=false
            :capture="false"
            accept="image/*"
            doNotResize="['gif', 'svg']"
            @input="setCoverImage"
            @onUpload="setCoverImage"
            @onComplete="setCoverImage"
          ></ImageUploader>
        </b-form-group>
        <div class="buttons">
            <b-button @click="reset">Cancelar</b-button>
            <b-button v-if="!edit" variant="success" @click="create">Criar</b-button>
            <b-button v-if="edit" variant="success" @click="update">Editar</b-button>   
        </div>
        
      </div>
      <div class="category-list">
        <Loading v-if="loading" />
        <b-table v-else striped hover :items="items" :fields="fields">
          <template slot="editar" slot-scope="data">
              <b-button variant="warning"  @click='edit = true; new_category = data.item' style="margin: 0 15px;">
                  <i class="fa fa-pencil"></i>
              </b-button>
            </template>
            <template slot="image" slot-scope="data">
                <img v-bind:src="data.item.image" style="width: 70px;" />                   
          </template>
        </b-table>
      </div>
    </div>
</template>

<script>
import axios from 'axios';
import {url, showError} from '@/config/global';
import Loading from "@/components/Loading";
import ImageUploader from 'vue-image-upload-resize'
export default {
  name: 'AdminCategory',
  components: {
    Loading, ImageUploader
  },
  data: function () {
        return {
            loading: 0,
            fields: ["image","name", "title", "editar"],
            items: [],
            edit: false,
            new_category: {
              name: null,
              title: null,
              image: null
            },
            imageBytes: null
        }
  },
  methods: {
      setCoverImage: function (file) {
        this.hasImage = true
        if (file != undefined) {
            this.new_category.image = file.dataUrl
        }
      },
      loadCategories() {
          this.loading = 1
          axios.get(`${url}/category`)
              .then(res => {
                this.items = res.data
                this.loading = 0
              })
              .catch(showError)
      },
      create(){
         axios.post(`${url}/category`,this.new_category)
          .then(() => {
                this.loadCategories()
                this.$toasted.global.defaultSuccess({msg:"Categoria cadastrada!"})
                this.reset()
              })
              .catch(showError)
              
      },
      update(){
          axios.put(`${url}/category`,this.new_category)
          .then(() => {
            this.loadCategories()
            this.$toasted.global.defaultSuccess({msg:"Categoria atualizada!"})
            this.reset()
          })
          .catch(showError)
              
      },
      remove(item){
        axios.put(`${url}/category`,{...item, 'removed': !item.removed})
          .then(() => {
            this.loadCategories()
            this.$toasted.global.defaultSuccess({msg:"Categoria atualizada!"})
            this.reset()
          })
          .catch(showError)
      },
      reset(){
        this.edit = false;
        this.new_category = {
          name: null,
          title: null
        }
      }
  },
  mounted() {
      this.loadCategories()
  },
  created() {
      this.loadCategories()
  }
}
</script>

<style>
.category {
  padding: 30px;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  flex-direction: row;
}
.category-list{
  width: 70%;
}
.category-create {
  width: 25%;
}
.category-create .buttons{
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.category-item{
  display: flex;
  justify-content: space-between;
}
.category-item > i:hover{
  cursor: pointer;
}

.category .create{
  padding: 50px;
}
.category .create .buttons{
  display: flex;
  justify-content: space-between;
}
</style>
