<template>
  <Loading class="loading" v-if="loading" />
  <b-container v-else class="categories">
    <b-row>
        <b-col v-for="item in categories" :key="item._id" class="category" >
            <h2>{{item.title}}</h2>
            <img v-bind:src="item.image" />
        </b-col>
    </b-row>
  </b-container>
</template>

<script>
import axios from 'axios';
import {url, showError} from '@/config/global';
import Loading from "@/components/Loading";
export default {
  name: 'Categories',
  components: {
    Loading
  },
  data: function () {
        return {
            categories: [],
            loading: 0
        }
  },
  methods: {
      loadCategories() {
          this.loading = 1
          axios.get(`${url}/category`)
          .then(res => {
            this.categories = res.data
            this.loading = 0
          }).catch(showError)
      }
  },
  beforeMount () {
      this.loadCategories()
  }
}
</script>

<style>
.categories{
  padding-top: 15px;
}
.categories .category {
  margin: 5px;
  box-shadow: 5px 5px 17px -5px rgba(0,0,0,0.7);
  display: flex;
  align-items: center;
  text-align: center;
  flex-direction: column;
  justify-content: center;
  padding: 10px;
  border-radius: 5px;
}

.categories .category:hover{
  cursor: pointer;
  background-color: rgba(0,0,0,0.3);
}
</style>
